import { createWebHistory, createRouter } from "vue-router";
import Home from './components/Home.vue';
import Quiz from './components/Quiz.vue';
import Quiz14 from './components/Quiz14.vue';
import QuizReady from './components/QuizReady.vue';
import Quiz14Result from './components/Quiz14Result.vue';

const routes = [
    {
        path: "/home",
        name: 'Home', // 이름 추가
        component: Home,
    },
    {
        path: "/quiz",
        name: 'Quiz', // 이름 추가
        component: Quiz,
    },
    {
        path: "/quizready",
        name: 'QuizReady', // 이름 추가
        component: QuizReady,
    },
    {
        path: "/quiz/quiz14",
        name: 'Quiz14', // 이름 추가
        component: Quiz14,
    },
    {
        path: "/quiz/quiz14result",
        name: 'Quiz14Result', // 이름 추가
        component: Quiz14Result,
    },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;
