<template>
  <div>
    <div style="background-color: #154a9a; display: flex; justify-content: center; align-items: center; padding: 22px">
      <img style="height: 55px" src="https://imgur.com/r3sb9xf.png">
    </div>

    <div class="container">
      <div class="question-section">
        <div class="question-container" v-for="(question, index) in questions" :key="index">
          <div class="question-header">문제 {{ index + 1 }}</div>
          <div class="question-text">{{ question.text }}</div>
          <div class="options" v-if="question.type === 'multiple-choice'">
            <div class="option" v-for="(option, idx) in question.options" :key="idx">
              <input type="radio" :id="'question' + index + 'option' + idx" :name="'question' + index">
              <label :for="'question' + index + 'option' + idx">{{ option }}</label>
            </div>
          </div>
          <div class="answer-input" v-if="question.type === 'text'">
            <input type="text" v-model="question.answer" placeholder="정답을 입력하세요">
          </div>
        </div>
        <button class="submit" @click="submitQuiz" style="background-color: #6A67CE">제출하기</button>
      </div>
      <div class="timer">
            시간 : {{ minutes }}분{{ seconds < 10 ? '0' : '' }}{{ seconds }}초
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Quiz14-Quiz14',
  data() {
    return {
      questions: [
        {
          text: '제록스 PARC에서 개발한 Alto 컴퓨터에 관한 설명 중 틀린 것을 고르세요.',
          type: 'multiple-choice',
          options: [
            '세계 최초로 이더넷을 사용했다.',
            '비트맵 방식의 디스플레이를 사용했다.',
            '세계 최초의 위치기반 윈도우제어 소프트웨어를 탑재했다.',
            '세계 최초로 마우스를 사용했다.'
          ]
        },
        {
          text: '제록스 PARC에서 개발한 영상 이미지 편집기로서, 비디오 영상을 캡처하고 그 이미지의 Hue나 Saturation을 변경하고 이미지 정보를 수정하는 등의 기능을 제공한 시스템의 이름은 ______이다. (영어로 작성하세요.)',
          type: 'text',
          answer: ''
        },
        {
          text: 'Apple II 컴퓨터를 위해서 개발된 최초의 스프레드시트(spreadsheet) 프로그램의 이름은? (영어로 작성하세요.)',
          type: 'text',
          answer: ''
        },
        {
          text: '다음 중 맞는 것을 고르세요.',
          type: 'multiple-choice',
          options: [
            '내장형 컴퓨터는 프로그래밍을 할 수 없는 컴퓨터이다.',
            'Apple II 컴퓨터의 운영체제는 MS-DOS이다.',
            '제록스 PARC에서 개발한 브라보 소프트웨어는 최초로 copy-and-paste를 구현했다.',
            'IBM PC 컴퓨터는 개방형 구조의 개인용 컴퓨터이다.'
          ]
        },
      ],
      time: 60, // 1분 = 60초
      timer: null
    };
  },
  computed: {
    minutes() {
      return Math.floor(this.time / 60);
    },
    seconds() {
      return this.time % 60;
    }
  },
  mounted() {
    this.startTimer();
    // 55초 후에 알림을 표시
    setTimeout(() => {
      alert('곧 시험이 종료됩니다');
    }, 55000); // 55초 = 55000밀리초
  },
  methods: {
    startTimer() {
      this.timer = setInterval(() => {
        if (this.time > 0) {
          this.time--;
        } else {
          clearInterval(this.timer);
          alert('시간이 다 되었습니다. 시험이 종료됩니다.');
          this.submitQuiz();
        }
      }, 1000);
    },
    submitQuiz() {
      if (confirm('제출하시겠습니까?')) {
        this.$router.push({ name: 'Quiz14Result' });
      }
    }
  },
  beforeUnmount() {
    clearInterval(this.timer);
  }
};
</script>

